@import "../../styles/variables";
.circle-container {
  transform: translate(-50%, 0);
  left: 50%;
  .circle-line {
    height: 1px;
    width: 100px;
    background-color: color("darkGray");
  }
  .circle-box {
    height: 10px;
    width: 10px;
    border-radius: 50%;
    background-color: color("darkGray");
  }
  .circle-selected {
    background-color: color("blue");
  }
}
